









































































































@import '../styles/variables.scss';

.projects {

    .animatedLetters {
        @include fontXXL;
        font-weight: $fontBold;
        margin-bottom: 40px;
    }

    ul {
        width: 100%;
        display: grid;
        grid-template: auto / repeat(2, minmax(200px, 500px));
        grid-gap: 100px 3%;
        justify-content: space-around;
    }

    figure {
        width: 100%;
        height: 100%;
        position: relative;
        overflow: hidden;

        &:before {
            content: attr(text);
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            background: $black;
            opacity: .15;
            @include transition (opacity, .4s); // should be equal or higher than the direction aware animation (on <span>)
            z-index: 1;
        }

        &:hover:before {
            opacity: .5;
        }

        span { // overlay
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            @include fontGiant ($black);
            font-weight: $fontBold;
            letter-spacing: 6.5px; // So that 'Corporate' fits
            text-align: center;
            background: $white;
            mix-blend-mode: color-dodge;
            transform: translate3d(0, -100%, 0);
            z-index: 1;

            &:before {
                content: attr(projectName);
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-transform: uppercase;
            }

        }

        img {
            width: 100%;
            height: 100%;
            position: relative;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

    }

    @include phablet {
        figure span {
            @include fontXXL ($black);
        }
    }

    @include mq (0, 569px) {
        padding: calc(#{$navHeight + 20}) 0 calc(#{$footerHeight + 20});

        ul {
            grid-template-columns: 1fr;
            grid-gap: 50px;
        }

        li.slide-in-top figure:before, li.slide-in-bottom figure:before {
            opacity: .5;
        }

        figure {

            &:hover:before {
                opacity: .15;
            }

            span {
                font-size: $fontGiantMobile;
                line-height: 40px;
                letter-spacing: 5px;
            }

        }
    }

}

