
























@import '../styles/variables.scss';

.content {
    width: 100%;
    display: flex;
    position: relative;
    padding: calc(#{$navHeight + 100}) 0 calc(#{$footerHeight + 40});

    h1 {
        width: 60%;
        @include fontXXL;
        margin-bottom: 40px;
    }

    p {
        width: 60%;
        @include fontXL;
    }

    .animatedLetters {
        width: auto;
        display: inline-block;
        color: $blue;
    }

    @include tablet {
        h1, p:not(.animatedLetters) {
            width: 100%;
        }
    }

}

