


































































































































































































































































































































































@import '../styles/variables.scss';

$identation1: 40px;
$identation2: #{$identation1 * 2};
$identation3: #{$identation1 * 3};

.aboutMe {

    .animatedLetters {
        @include fontXXL;
        font-weight: $fontBold;
        margin-bottom: 40px;
    }

    code {
        display: block;
        position: relative;
        @include fontM;
        margin-left: 60px;

        &:before {
            content: '';
            width: 5px;
            height: 100%;
            position: absolute;
            top: 0;
            left: -20px;
            background: $white;
        }

        p {
            min-height: 26px;
            position: relative;
            z-index: 1;

            &.identation1 {
                padding-left: $identation1;
            }

            &.identation2 {
                padding-left: $identation2;
            }

            &.identation3 {
                padding-left: $identation3;
            }

            &:before {
                content: attr(linenumber);
                position: absolute;
                top: 0;
                left: -50px;
                color: #aaa;
            }

            &:after {
                content: '';
                width: 1000vw;
                height: 100%;;
                position: absolute;
                top: 0;
                left: -100%;
                background: rgba($white, .1);
                opacity: 0;
                z-index: -1;
            }

            &:hover:after {
                opacity: 1;
            }

            .yellow {
                color: $aboutMeYellow;

                .link {
                    color: inherit;

                    &:before {
                        background: $aboutMeYellow;
                    }

                }
            }

            .orange {
                color: $aboutMeOrange;
            }

            .red {
                color: $aboutMeRed;
            }

            .purple {
                color: $aboutMePurple;
            }

            .green {
                color: $aboutMeGreen;
            }

            .lightblue {
                color: $aboutMeLightBlue;
            }

            .teste {
                display: inline-block;
                position: relative;
                transform-style: preserve-3d;
                @include transition (all, .4s);

                &:before {
                    content: '25yo';
                    width: 100%;
                    height: 100%;
                    position: absolute;
                    top: -100%;
                    transform-origin: 0% 100%;
                    transform: rotateX(90deg);
                }

                &:hover {
                    transform-origin: 50% 100%;
                    transform: rotateX(-90deg) translateY(100%);
                }

            }

        }

    }

    @include tablet {
        code {
            margin-left: 30px;

            &:before {
                left: -14px;
            }

            p:before {
                left: -40px;
            }

        }
    }

}
