




































































@import '../styles/variables.scss';

.projectDetail {

    .wrapper {
        position: relative;
        padding: 0 50px;

        &:before {
            content: '';
            width: 5px;
            height: 100%;
            position: absolute;
            top: 20px;
            left: 20px;
            background: $lightGrey;
            opacity: .3;
        }

    }

    .breakpointCircle {
        position: relative;

        &:before {
            content: '';
            width: 17px;
            height: 17px;
            position: absolute;
            top: 50%;
            left: -36px;
            transform: translateY(-50%);
            background: $lightGrey;
            border-radius: 50%;
        }

    }

    .animatedLetters {
        position: relative;
        margin-bottom: 70px;
    }

    &__description {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-column-gap: 2%;

        > article {
            padding-left: 20px;
        }

    }

    &__mainImage {
        width: 100%;
        grid-row: span 2;
        position: relative;
        top: 0;
        right: 0;
        overflow: hidden;

        img {
            width: 100%;
            height: auto;
        }

    }

    article {
        width: 100%;
        @include fontM;
        margin-bottom: 40px;

        h2 {
            @include fontL ($darkOrange);
            letter-spacing: 2.5px;
            text-transform: uppercase;
            margin-bottom: 10px;
        }

    }

    &__detailImage {
        width: 100%;
        position: relative;
        top: 0;
        right: 0;
        overflow: hidden;

        img {
            width: 100%;
            height: auto;
        }

    }

}

