




































































































@import '../styles/variables.scss';

.formComponent {

    > button {
        @include fontXL;
    }

    form {
        max-width: 700px;
        display: flex;
        flex-direction: column;
        position: relative;
        background: url('../images/background.jpg') no-repeat center/cover;
        margin: auto;
        padding: 30px;

        .animatedLetters {
            @include fontM;
            margin-bottom: 0;
        }

        input, textarea {
            @include fontM ($darkGrey);
            background: $lightGrey;
            border: 2px solid transparent;
            margin-top: 20px;
            padding: 5px 0 5px 10px;
            cursor: text;

            &.error {
                border: 2px solid $red;

                + .errorText {
                    opacity: 1;
                    pointer-events: initial;
                }

            }
        }

        textarea {
            height: 200px;
            margin-top: 46px;
        }

        // Placeholders
        ::-webkit-input-placeholder {
            @include fontM ($darkGrey);
            font-style: italic;
        }
        ::-moz-placeholder {
            @include fontM ($darkGrey);
            font-style: italic;
        }
        :-moz-placeholder {
            @include fontM ($darkGrey);
            font-style: italic;
        }
        :-ms-input-placeholder {
            @include fontM ($darkGrey);
            font-style: italic;
        }

        :focus::-webkit-input-placeholder {
            opacity: 0;
            @include transition (opacity, .3s, ease);
        }

        .errorText {
            opacity: 0;
            pointer-events: none;
            @include fontM ($red);
        }

        button {
            display: block;
            background: $blue;
            margin: 20px 0 auto auto;
            padding: 5px 25px;
            cursor: pointer;

            .animatedLetters {
                font-weight: $fontBold;
                margin-bottom: 0;
            }

        }

    }

    iframe {
        display: none;
    }

    .formNotification {
        width: 100%;
        position: fixed;
        left: 0;
        bottom: 0;
        transform: translateY(100%);
        @include fontL;
        text-align: center;
        padding: 30px;
        @include transition (transform, .5s);
        z-index: 1;

        &.success {
            background: #28ca80;
            transform: translateY(0);
        }

    }
}
